import { actionTypesSavable } from './actionTypes.js';

export function removeList(listId) {
  return {
    type: actionTypesSavable.REMOVE_LIST,
    listId
  }
}

export function positionListAsFirst(listId) {
  return {
    type: actionTypesSavable.POSITION_AS_FIRST,
    listId
  }
}

export function shareListWithConnection(listId, connectionId) {
  return {
    type: actionTypesSavable.SHARE_LIST_WITH_CONNECTION,
    listId,
    connectionId
  }
}

export function addNewList() {
  return {
    type: actionTypesSavable.ADD_NEW_LIST,
  }
}



