import React from 'react';
import PropTypes from 'prop-types';
import BaseComponent from '../../app_level/BaseComponent/';
import constants from '../../app_level/constants.jsx';
import utils from '../../app_level/utils.jsx';
import './login_form.css';

export default class ProductInputForm extends BaseComponent {
  constructor(props) {
    super(props);
    
    this.bindMethod('onLoginButtonClick');
  }
  
  onLoginButtonClick() {
    if (this.loginField.value && this.passwordField.value) {
      utils.setCookie('UNFORGETTABLES_AUTH_TOKEN', 1);
      this.props.reportLogin();
    }
  }
  
  render() {
    return (
      <div className='login_form_wrapper'
           id='unf_app'>
        <div className='login_form_description'>{constants.loginPage.greeting}</div>

        <a className='login_form_button'
           href='/auth/google'>
          {constants.loginPage.loginButtonText}
        </a>
      </div>
    )
  }
}

ProductInputForm.propTypes = {
  reportLogin: PropTypes.func.isRequired
};



