import 'rxjs';
import {combineEpics} from 'redux-observable';
import { actionTypesSavable, actionTypesUtil } from '../actions/actionTypes.js';
import utils from '../../components/app_level/utils.jsx';

function saveToServerEpic(action$, store) {
  return action$
    .filter(action => !!actionTypesSavable[action.type] && action.saveNeeded !== false)
    .debounceTime(500)
    .do(() => {
      const {productLists, settings} = store.getState();
      utils.saveState({productLists, settings});
    })
    .mapTo({ type: actionTypesUtil.SAVE_SUCCESS });
}

export const rootEpic = combineEpics(saveToServerEpic);

