const utils = {
  createUniqueId() {
    function s4() {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    }

    return `${s4()} - ${s4()} - ${s4()} - ${s4()} - ${s4()}`;
  },

  mockCheckLocalAuthToken(token) {
    return !!token;
  },

  isLoggedOn() {
    const localAuthToken = utils.getCookie('UNFORGETTABLES_AUTH_TOKEN');
    return this.mockCheckLocalAuthToken(localAuthToken);
  },

  getCookie(name) {
    const nameEQ = `${name}=`;
    const cookiesArray = document.cookie.split(';');

    for (let i = 0; i < cookiesArray.length; i++) {
      let currentCookie = cookiesArray[i];
      while (currentCookie.charAt(0) === ' ') {
        currentCookie = currentCookie.substring(1, currentCookie.length)
      }

      if (currentCookie.indexOf(nameEQ) === 0) {
        return currentCookie.substring(nameEQ.length, currentCookie.length)
      }
    }
    return null;
  },

  setCookie(name, value, expiration) {
    const longLasting = 987654321000;
    const expirationDate = expiration || longLasting;
    const date = new Date();
    date.setMilliseconds(expirationDate);
    document.cookie = `${name}=${value};expires=${date.toGMTString()};`;
  },

  eraseCookie(name) {
    this.setCookie(name, '', -1);
  },

  loadState() {
    try {
      const persistedState = localStorage.getItem('unforgettables_state');
      if (!persistedState) {
        return null;
      }
      return JSON.parse(persistedState);
    } catch (err) {
      return null;
    }
  },

  saveState(state) {
    try {
      const persistedState = JSON.stringify(state);
      localStorage.setItem('unforgettables_state', persistedState);
    } catch (err) {
      console.log(err);
    }
  },

  createNewProductList() {
    return {
      id: utils.createUniqueId(),
      title: 'My new List',
      sharedWith: [],
      creationDate: Date.now(),
      lastAmendmentsDate: Date.now(),
      products: [],
    };
  },

  setCursorToEnd(e) {
    const val = e.target.value;
    e.target.value = '';
    e.target.value = val;
  },
};

export default utils;



