import React from 'react';
import { Router, Route, IndexRoute } from 'react-router';
import { Provider } from 'react-redux';
import Raven from 'raven-js';
import store, { history } from './store.js';
import ListViewContainer from './components/list_view/ListViewContainer/';
import SettingsPage from './components/settings_page/SettingsPageContainer/';
import MainContainer from './components/app_level/MainContainer'
import utils from './components/app_level/utils.jsx';
import BaseComponent from './components/app_level/BaseComponent/';
import MyListsPage from './components/my_lists_page/MyListsPage/';
import LoginForm from './components/login_page/LoginForm';
import LoadingDataInfo from './components/login_page/LoadingDataInfo';
import { actionTypesUtil } from './redux/actions/actionTypes.js';

Raven
  .config('https://b7d024612a83494aaded2648cb9b0ade@sentry.io/274854')
  .install();

export default class App extends BaseComponent {
  constructor(props) {
    super(props);
    
    this.state = {
      isLoggedOn: utils.isLoggedOn(),
      contentIsLoaded: false
    };
  
    this.bindMethod('logIn', 'logOut');
  }

  componentDidMount() {
    if (this.state.isLoggedOn && !this.state.contentIsLoaded) {
      this.mockLoadUserContent();
    }
  }
  
  logIn() {
    this.mockLoadUserContent();
    this.setState({isLoggedOn: true});
  }
  
  logOut() {
    this.setState({isLoggedOn: false, contentIsLoaded: false});
  }

  mockLoadUserContent() {
    setTimeout(() => {
      const userData = utils.loadState();
      if (userData) {
        store.dispatch({
          type: actionTypesUtil.PROVIDE_USER_DATA,
          userData
        });
      }
      this.setState({contentIsLoaded: true});
    }, 500);
  }
  
  getAppContent() {
    return (
      <Provider store={store}>
        <Router history={history}>
          <Route path="/" component={MainContainer}>
            <IndexRoute components={{pageContent: ListViewContainer}} />
            <Route path="/my_lists" components={{pageContent: MyListsPage}} />
            <Route path="/settings" components={{pageContent: SettingsPage}} logOut={this.logOut} />
          </Route>
        </Router>
      </Provider>
    );
  }
  
  showLogonForm() {
    return <LoginForm reportLogin={this.logIn} />;
  }
  
  render() {
    const appContent = this.state.contentIsLoaded ? this.getAppContent() :
      this.state.isLoggedOn ? <LoadingDataInfo /> : this.showLogonForm();
    
    return (
      <div>
        {appContent}
      </div>
    )
  }
}



