import { actionTypesSavable } from './actionTypes.js';

export function changeCurrentListTitle(newTitle, saveNeeded) {
  return {
    type: actionTypesSavable.CHANGE_CURRENT_LIST_TITLE,
    newTitle,
    saveNeeded,
  }
}

export function clearCurrentList() {
  return {
    type: actionTypesSavable.CLEAR_CURRENT_LIST
  }
}

export function deleteSelectedProducts(productsToDelete, saveNeeded) {
  return {
    type: actionTypesSavable.DELETE_SELECTED_PRODUCTS,
    productsToDelete,
    saveNeeded,
  }
}

export function addProduct(productData, productPosition) {
  return {
    type: actionTypesSavable.ADD_PRODUCT,
    productData,
    productPosition
  }
}

export function markProductAsBought(productId) {
  return {
    type: actionTypesSavable.MARK_PRODUCT_AS_BOUGHT,
    productId
  }
}

export function editProduct(productData, saveNeeded) {
  return {
    type: actionTypesSavable.EDIT_PRODUCT,
    productData,
    saveNeeded,
  }
}



